
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');
body {
  font-family: 'Poppins', sans-serif;
  background-color: #FCFAEE;
}
/* h1{
  font-family: 'Poppins', sans-serif;
  font-weight: 400px;
} */
h2, h5, p {
  font-family: 'Poppins', sans-serif;
  /* font-weight: 400; */
}


.section {
  padding: 50px 0;
}

.section-title {
  font-size: 2rem;
  margin-bottom: 30px;
  text-align: center;
}

.text-animate {
  animation: slideIn 1.5s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-100%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
.timeline {
  position: relative;
  padding: 40px 0;
}

.timeline-item {
  position: relative;
  margin-bottom: 40px;
}

.timeline-item .timeline-icon {
  position: absolute;
  left: -50px;
  top: 0;
  width: 30px;
  height: 30px;
  background-color: green; /* Customize color */
  border-radius: 50%;
  text-align: center;
  line-height: 30px;
  color: black;
}

.timeline-item .timeline-content {
  margin-left: 50px;
  padding: 20px;
  background-color: #EFE3C2;
  border-radius: 8px;
}

.timeline-item .timeline-content h5 {
  font-size: 1.25rem;
  font-weight: bold;
}

.timeline-item .timeline-content p {
  font-size: 1rem;
}

.timeline-item .timeline-icon.bg-primary {
  background-color: #00ff15;
}

.timeline-item .timeline-icon.bg-secondary {
  background-color: #6c757d;
}
